#admin-page {
  padding-top: 0;
}

.input-no-spinner::-webkit-outer-spin-button,
.input-no-spinner::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}


input[type=number] {
  border-bottom: none;
  height: 100px;
  font-size: 40px;
  margin-bottom: 0px;
  background-color: #f2f2f2;
}

.numbers-padding-top{
  padding-top: 9px;
}

.input-sides {
  opacity: 0.3;
}

